.ant-table-cell {
    text-align: center;
    font-size: 18px;
}

.ant-table-thead > tr > th {
    text-align: center;
}

.ant-table-cell--color {
    min-width: 70px;
    height: 70px;
}

@media (max-width: 600px) {
    .ant-table-cell {
        font-size: 12px;
    }
    .ant-table-thead > tr > th {
        font-size: 12px;
        padding: 5px;
    }
    .ant-table-thead > tr > td {
        padding: 5px;
    }
}

img.role-icon {
    width: 50px;
    height: 50px;
}

.roles-icons {
    display: flex;
}

